import WOW from 'wowjs';
import $ from "jquery";


$.when( $.ready ).then(function() {
  $('.quantity').each(function() {
      var spinner = $(this),
        input = spinner.find('input[type="number"]'),// 
        btnUp = spinner.find('.quantity-up'),//  
        btnDown = spinner.find('.quantity-down'),//  
        min = input.attr('min'),//   .
        max = input.attr('max');//   .

      btnUp.on('click', function() {//  
        var oldValue = parseFloat(input.val());//   value     
        if (oldValue >= max) { // value >= max 
          var newVal = oldValue;//  ,   value
        } else {
          var newVal = oldValue + 1;//k value  
        }
        spinner.find("input").val(newVal);//
        spinner.find("input").trigger("change");// change       input; trigger    change
      });

      btnDown.on('click', function() {
        var oldValue = parseFloat(input.val());
        if (oldValue <= min) {
          var newVal = oldValue;
        } else {
          var newVal = oldValue - 1;
        }
        spinner.find("input").val(newVal);
        spinner.find("input").trigger("change");
      });

    });

    $('.quantity-up').on('click', function() {
      let par = $(this).parent();
      let inp = par.prev();
      inp.removeClass('extras');

      //var quant = $(this);
      //console.log(inp)
      //input = quant.parent();// 
      //console.log(input);
      //input.toggleClass('extras-visible');

    })
    $('.quantity-down').on('click', function() {
      let par = $(this).parent();
      let inp = par.prev();
      let valIn = parseFloat(inp.val());
      //console.log(valIn);
      if (valIn == 0) {
        inp.addClass('extras')
      }

      //var quant = $(this);
      //console.log(inp)
      //input = quant.parent();// 
      //console.log(input);
      //input.toggleClass('extras-visible');

    })



    new WOW.WOW({
      live: false//     
     //    -   
    }).init();

});




